import { dream as $ } from './components/Assistant';

import Scroll from './components/Scroll';

const scroll = new Scroll();

scroll.init();

const handleNavDrawer = () => {

    const toggle = $('#nav-toggle');
    const icon = $('#nav-icon');
    const branding = $('#nav-brand');

    toggle.on('change', e => {

        icon.toggle('open', e.target.checked);

        if (e.target.checked) branding.hide();
        else branding.show();

    });

}

handleNavDrawer();